(function() {
    'use strict';

    ngApp.directive('ngSwitch', function($websocket) {
        'ngInject';

        return {
            restrict: 'EA',
            replace: true,
            transclude: true,
            scope: {

                ngModel: '=',
                ngData: '=',

                disabled: '=',

                ngTrueValue: '=',
                ngFalseValue: '=',


                ngBeforeChange: '&',
                ngAfterChange: '&'
            },
            template: function(element, attrs) {
                var html = '<span class="switch" ng-click="onClick($event)" ng-class="{checked: checked, disabled: disabled}"><small></small><input type="checkbox" ng-model="' + attrs.ngModel + '" style="display:none" /></span>';

                return html;
            },
            link: function(scope, element, attrs) {
                
                scope.onClick = function($event) {

                    if(scope.disabled){
                        return;
                    }

                    if (!scope.ngBeforeChange($event, scope.ngData)) {
                        return;
                    };

                    calc();
                    refresh();

                    scope.ngAfterChange($event, scope.ngData);
                };

                scope.$watch('ngModel', function() {
                    refresh();
                })



                var calc = function() {
                    //计算model
                    scope.ngModel = scope.ngModel == scope.ngTrueValue ? scope.ngFalseValue : scope.ngTrueValue;
                }
                var refresh = function() {
                    //计算选中状态
                    scope.checked = scope.ngModel == scope.ngTrueValue ? true : false;
                };

                refresh();
            }
        };

    });

})();

(function() {
    'use strict';

    ngApp.directive('ngTerm', function($websocket) {
        'ngInject';

        return {
            restrict: 'EA',
            scope: {
                url: '=',

                logs: '@',
                stdin: '@',
                start: '='
            },
            template: '<div class="ng-term"></div>',
            replace: false,
            link: function(scope, element, attrs) {
                var inited = false;

                var init = function() {
                    var term = new Terminal({
                        cols: 100,
                        rows: 20,
                        convertEol: true,
                        useStyle: true,
                        cursorBlink: true,
                        screenKeys: true
                    });
                    var ws = $websocket(scope.url);

                    ws.onMessage(function(message) {
                        term.write(message.data);
                    });
                    ws.onClose(function() {
                        term.destroy();
                    });
                    ws.send('\n');

                    term.open(element.find('.ng-term')[0]);
                    term.on('data', function(message) {
                        ws.send(message);
                    });

                    element.on('$destroy', function() {
                        term.destroy();
                    });

                    inited = true;
                }

                if (scope.start) {
                    init();
                }

                if (!inited) {
                    var stopWatch = scope.$watch('start', function() {
                        if (scope.start) {
                            init();
                            stopWatch();
                        }
                    });
                }



            }
        };

    });

})();

(function() {
    'use strict';

    ngApp.filter('FileSize', function() {
        'ngInject';

        return function(bytes, precision, rate) {
            if (isNaN(parseFloat(bytes)) || !isFinite(bytes) || bytes==0) return '-';
            if (typeof precision === 'undefined') precision = 1;
            if (typeof rate === 'undefined') rate = 1024;
            var units = ['bytes', 'kB', 'MB', 'GB', 'TB', 'PB'],
                number = Math.floor(Math.log(bytes) / Math.log(rate));
            return (bytes / Math.pow(rate, Math.floor(number))).toFixed(precision) + ' ' + units[number];
        }

    });

})();

