(function() {
    'use strict';

    ngApp.factory('dockerApiSvr', function($rootScope, $http, CONF) {
        'ngInject';

        var dockerUrl = CONF.SVR_URL.base;

        $rootScope.cancelEvent = function(event) {
            console.log('cancelEvent');
            var event = event || window.event; //用于IE  
            if (event.preventDefault) event.preventDefault(); //标准技术  
            if (event.returnValue) event.returnValue = false; //IE  
            return false; //用于处理使用对象属性注册的处理程序  
        }


        var svr = {};

        svr.info = function(cb) {
            var url = dockerUrl + '/info';
            var p = $http({
                method: 'GET',
                url: url
            });
            p.success(function(rsp) {
                cb(rsp);
            });
        };

        svr.containers = function(cb) {
            var url = dockerUrl + '/containers/json';
            var p = $http({
                method: 'GET',
                url: url
            });
            p.success(function(rsp) {
                cb(rsp);
            });
        };
        svr.containers_start = function(id, cb) {
            var url = dockerUrl + '/containers/' + id + '/start';
            var p = $http({
                method: 'POST',
                url: url
            });
            p.success(function(rsp) {
                cb(rsp);
            });
        };
        svr.containers_stop = function(id, cb) {
            var url = dockerUrl + '/containers/' + id + '/stop';
            var p = $http({
                method: 'POST',
                url: url
            });
            p.success(function(rsp) {
                cb(rsp);
            });
        };
        svr.containers_inspect = function(id, cb) {
            var url = dockerUrl + '/containers/' + id + '/json';
            var p = $http({
                method: 'GET',
                url: url
            });
            p.success(function(rsp) {
                cb(rsp);
            });
        };
        svr.exec_start = function(id, cb) {
            var url = dockerUrl + '/exec/' + id + '/start';
            var p = $http({
                method: 'POST',
                url: url,
                headers: {
                    'Content-Type': 'application/json'
                },
                data: {
                    Detach: false,
                    Tty: true
                }
            });
            p.success(function(rsp) {
                cb(rsp);
            });
        };

        svr.images = function(cb) {
            var url = dockerUrl + '/images/json';
            var p = $http({
                method: 'GET',
                url: url
            });
            p.success(function(rsp) {
                cb(rsp);
            });
        };

        svr.images_search = function(param, cb) {
            var url = dockerUrl + '/images/search';
            var p = $http({
                method: 'GET',
                url: url,
                params: param
            });
            p.success(function(rsp) {
                cb(rsp);
            });
        };
        svr.images_create = function(data, cb) {
            var url = dockerUrl + '/images/create';
            var p = $http({
                method: 'POST',
                url: url,
                data: data
            });
            p.success(function(rsp) {
                cb(rsp);
            });
        };



        return svr;
    });

})();

(function() {
    'use strict';

    ngApp.factory('regexTranslationHandler', function($translate) {
        'ngInject';



        return function(translationID) {
            var result = translationID;

            var translationTable = $translate.getTranslationTable();

            //key: up on (.*) hours, next (.*) days
            //translationID: up on 5 hours, next 6 days
            //result: 超过 {{1}} 小时，等待 {{2}} 天
            for (var key in translationTable) {
                var regex = new RegExp(key).exec(translationID);
                if (regex == null) {
                    continue;
                }
                result = translationTable[key];

                for (var i = 1; i < regex.length; i++) {
                    result = result.replace('{{' + i + '}}', regex[i]);
                }
            }
            return result;
        }

    });

})();

